.widgetLarge{
    flex: 2;
    -webkit-box-shadow: -1px 6px 15px 1px rgba(0,0,0,0.1); 
    box-shadow: -1px 6px 15px 1px rgba(0,0,0,0.1);
    padding: 20px;
}

.widgetLargeTtitle{
    font-size: 22px;
    font-weight: 600;
}

.widgetLargeTable{
    width: 100%;
    border-spacing: 20px;
}

.widgetLargeTh{
    text-align: left;
}

.widgetLargeUser{
    display: flex;
    align-items: center;
    font-weight: 600;
}

.widgetLargeImg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}

.widgetLargeDate,.widgetLargeAmount{
    font-weight: 400;
}

.widgetLargeButton{
    padding: 5px 7px;
    border: none;
    border-radius: 10px;
}

.widgetLargeButton.Approved{
    background-color: #e5faf2;
    color: #3bb077;
}

.widgetLargeButton.Declined{
    background-color: #fff0f1;
    color: #d95087;
}

.widgetLargeButton.Pending{
    background-color: #ebf1fe;
    color: #2a7ade;
}

